<template>
	<div class="music">
		<Hot title="">
			<AudioItem v-for="(item,index) in arrPic" :url="item.url" :name="item.name" :score="item.score" :left="index * 28 + '%'">
				<span class="authorName">{{ item.authorName }}</span>
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_empty.png" alt="">
				<span>{{ item.score }}</span>
			</AudioItem>
		</Hot>
		<Hot title="">
			<AudioItem v-for="(item,index) in arrPic2" :url="item.url" :name="item.name" :score="item.score" :left="index * 28 + '%'">
				<span class="authorName">{{ item.authorName }}</span>
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_empty.png" alt="">
				<span>{{ item.score }}</span>
			</AudioItem>
		</Hot>		
		<AudioNews :url="url" :des="des"></AudioNews>
	</div>
</template>

<script type="text/javascript">
import Hot from './audio-hot'
import AudioNews from './audio-news'
import AudioItem from './audio-item'

	export default {
		components: {
			Hot,AudioNews,AudioItem
		},
		data: function () {
			return {
				url: require("../assets/images/musicNews.jpg"),
				des: "52, ",
				arrPic: [
					{url: require("../assets/images/music1.jpg"), name: "", score: "7.5", authorName: ""},
					{url: require("../assets/images/music2.jpg"), name: "", score: "7.6", authorName: ""},
					{url: require("../assets/images/music3.jpg"), name: " & ", score: "9.2", authorName: ""},
					{url: require("../assets/images/music4.jpg"), name: " ", score: "6.6", authorName: ""},
				],
				arrPic2: [
					{url: require("../assets/images/music5.jpg"), name: "Witness", score: "7.5", authorName: "Katy Perry"},
					{url: require("../assets/images/music6.jpg"), name: "After Sex", score: "7.6", authorName: "Katy Perry"},
					{url: require("../assets/images/music7.jpg"), name: "Relaxer", score: "9.2", authorName: "Katy Perry"},
					{url: require("../assets/images/music8.jpg"), name: "hopeless", score: "6.6", authorName: "Katy Perry"},
				],								
			}
		}
	}	
</script>

<style type="text/css">
.music .audioPic {text-align: left; height: 150px;}
.music .audioPic .name {padding: 0;}
.music .audioPic .item > img {width: 100%; height: auto;}
.music .authorName {display: block; padding-bottom: 3px;}
</style>


