<template>
	<div class="film">
		<Hot title="">
			<AudioItem v-for="(item,index) in arrPic" :url="item.url" :name="item.name" :score="item.score" :left="index * 28 + '%'">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_empty.png" alt="">
				<span>{{ item.score }}</span>
			</AudioItem>
		</Hot>
		<AudioNews :url="url" :des="des"></AudioNews>
		<Hot title="">
			<AudioItem v-for="(item,index) in arrPic2" :url="item.url" :name="item.name" :score="item.score" :left="index * 28 + '%'">
				<span>{{ item.score }}</span>
			</AudioItem>
		</Hot>		
	</div>
</template>

<script type="text/javascript">
import Hot from './audio-hot'
import AudioNews from './audio-news'
import AudioItem from './audio-item'

	export default {
		components: {
			Hot,AudioNews,AudioItem
		},
		data: function () {
			return {
				url: require("../assets/images/filmNews.jpg"),
				des: "",
				arrPic: [
					{url: require("../assets/images/film1.jpg"), name: "", score: "7.5"},
					{url: require("../assets/images/film2.jpg"), name: "5", score: "7.6"},
					{url: require("../assets/images/film3.jpg"), name: "", score: "9.2"},
					{url: require("../assets/images/film4.jpg"), name: "A", score: "6.6"},
				],
				arrPic2: [
					{url: require("../assets/images/film5.jpg"), name: "", score: 6367},
					{url: require("../assets/images/film6.jpg"), name: "52", score: 50},
					{url: require("../assets/images/film7.jpg"), name: "", score: 140},
					{url: require("../assets/images/film8.jpg"), name: "", score: 180},
				],				
			}
		}
	}	
</script>

<style type="text/css">
.film {}


</style>


